.timeline-container{
   
    padding: 1cm 0vw 0cm 0cm;
    box-sizing: border-box;
    
}


  /* Set a background color */
  // body {
  //   background-color: #474e5d;
  //   font-family: Helvetica, sans-serif;
  // }
  
  /* The actual timeline (the vertical ruler) */
  .timeline {
    position: relative;
    box-sizing: border-box;
    max-width: 1100px;
    margin: 0 auto;
    // border: 1px solid gray;
  }
  
  /* The actual timeline (the vertical ruler) */
  .timeline::after {
    content: '';
    position: absolute;
    width: 3px;
    background-color: #727272;
    top: 0;
    opacity: .5;
    bottom: 0;
    left: 50%;
    margin-left: -4px;
  }
  
  /* Container around content */
  .container {
    box-sizing: border-box;
    padding: 10px 30px;
    position: relative;
    background-color: inherit;
    width: 50%;
    
  }
 
  
  /* The circles on the timeline */
  .container::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    right: -9px;
    background-color: #9c2be7;
    border: 4px solid rgb(80, 0, 117);
    top: 15px;
    opacity: .5;
    border-radius: 50%;
    z-index: 1;
  }
  
  /* Place the container to the left */
  .left {
    left: 0;
  }
  
  /* Place the container to the right */
  .right {
    left: 50%;
  }
  
  /* Add arrows to the left container (pointing right) */
  .left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 20px;
    width: 0;
    z-index: 1;
    right: 22px;
    border: medium solid #737272;
    opacity: .5;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #737272;
  }
  
  /* Add arrows to the right container (pointing left) */
  .right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 20px;
    width: 0;
    z-index: 1;
    left: 20px;
    border: medium solid #737272;
    opacity: .5;
    border-width: 10px 10px 10px 0;
    border-color: transparent #737272 transparent transparent;
  }
  
  /* Fix the circle for containers on the right side */
  .right::after {
    left: -14px;
  }

  
  
  /* The actual content */
  .content {
    padding: 5px 20px 10px 20px;
    





    
    
    
  //  background-color: #0f0f13;
  backdrop-filter: blur(300px);
    // box-shadow: rgba(0, 0, 0, 0.500) 0px 2px 4px 0px, rgba(0, 0, 0, 0.500) 0px 4px 12px 4px;




    position: relative;
    border-radius: 20px;
    
    overflow-wrap: break-word;
    font-family: raleway;

    p{
      margin: 5px 0px;
      
    }
   
    
  }
 
  .timelineText{
    font-size: 20px;
    // color: rgba(197, 193, 193, 0.951);
    color: rgba(255, 255, 255);


    // font-weight: 600;
  }
  .timelineYear{
    font-size: 12px;
    // font-weight: 600;
    color: rgb(137, 137, 137);
    font-family: roboto;
  }
  .timelineTagLine{
    // font-weight: 600;
    font-size: 12px;
    font-family: raleway;
    // color: rgb(73, 73, 215);
    color: #bc65f1;

  }


  // light mode code
  .content-light{
    // background-color: rgb(240, 239, 239);
    // background-color: rgb(255, 255, 255); 
    backdrop-filter: blur(300px);

    border: none;
    
    // box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;

  }
  .timelineText-light{
    color: black;
  }
  .timelineTagLine-light{
    color: #9c2be7;

  }
  .timelineYear-light{
    color: rgb(23, 23, 23);

  }
  
  /* Media queries - Responsive timeline on screens less than 600px wide */
  @media screen and (max-width: 950px) {
  /* Place the timelime to the left */
    .timeline::after {
      left: 31px;
    }
    .downCV{
      width: 5cm;
      height: 1.8cm;
      font-size: 33px;
    }
  
  /* Full-width containers */
    .container {
      width: 100%;
      padding-left: 55px;
      padding-right: 25px;
    }
  
  /* Make sure that all arrows are pointing leftwards */
    .container::before {
      left: 45px;
      border: medium solid #9fc5d0;
      border-width: 8px 8px 8px 0;
      border-color: transparent #9fc5d0 transparent transparent;
    }
  
  /* Make sure all circles are at the same spot */
    .left::after, .right::after {
      left: 17px;
    }
  
  /* Make all right containers behave like the left ones */
    .right {
      left: 0%;
    }
  }

