* {
  margin: 0px 0px;
}

div,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: white;
}

button {
  user-select: none;
}

a {
  text-decoration: none;
}

.App {
  padding: 0vh 15vw;
  display: grid;
  // background-color: black;
  grid-template-rows: 2.1cm 1fr;
  height: 100vh;
  position: absolute;



}


.App::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("dk.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
  filter: brightness(50%);
}



.app-lower {
  overflow-x: auto;

}

.App-light {

  // background: rgb(238, 238, 238);
}

.App-light::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("sl.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
  filter: brightness(90%);
}


@media only screen and (max-width : 918px) {

  .App::before,
  .App-light::before {
    background-size: auto 100%;
  }
}

@media only screen and (max-width : 1042px) {

  
  .App-light::before {
    background-size: auto 100%;
  }
}


@media only screen and (min-width : 1650px) {
  .App {
    padding: 0vh 20vw;
  }
}

::-webkit-scrollbar {
  width: 0px;
  height: 0px;

}

::-webkit-scrollbar-track {
  background: #16161d;

}


::-webkit-scrollbar-thumb {
  background: rgb(64, 63, 63);

}


::-webkit-scrollbar-thumb:hover {
  background: rgb(51, 50, 50);

}